.App{
  text-align: center;
}
.movie-container{
  margin: 0 auto;
  width: 80%;
  border:1px solid #333;
  padding: 10px 0 30px 0;
  border-radius: 5px;
  margin-bottom: 50px;
}
.form-Wrapper{
  width: 80%;
  margin: 0 auto;
}
.title-input{
  width: 95%;
  height: 40px;
  margin: 10px;
  box-sizing: border-box;
}

.submit-button{
  width: 200px;
  height: 50px;
  font-size: 20px;
  padding: 20px;
  border: none;
  background: indianred;
  border-radius: 5px;
}
.ck.ck-editor__editable:not(.ck-editor__nested-editable) {
  min-height: 500px;
}